// //高德SDK
// import amap from '@/common/SDK/amap-wx.js';
export default {
	data() {
		return {
			showCategoryIndex:0,
			headerPosition:"fixed",
			city:"北京",
			//分类列表 李杰
			categoryList:'',
			// [
				// {id:1,title:'家用电器',banner:'/static/img/category/banner.jpg',list:[
				// 	{name:'冰箱',	img:'1.jpg'},
				// ]}
			// ]
		}
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		// this.amapPlugin = new amap.AMapWX({  
		// 	//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
		// 	key: '7c235a9ac4e25e482614c6b8eac6fd8e'  
		// });
		// //定位地址
		// this.amapPlugin.getRegeo({  
		// 	success: (data) => {
		// 		this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g,'');//把"市"去掉
		// 	}  
		// }); 
		// 获取所有的分类
		var self=this;
		uni.request({
			url:this.$url+'v1.catelist/index',
			success:function(res){
				self.categoryList=res.data;
			}
		})
		
		
	},
	methods: {
		//消息列表
		toMsg(){
			uni.navigateTo({
				url:'../../msg/msg'
			})
		},
		//分类切换显示
		showCategory(index){
			this.showCategoryIndex = index;
		},
		toCategory(e){
			uni.setStorageSync('catName',e.catename);
			uni.navigateTo({
				url: '../../goods/goods-list/goods-list?cid='+e.id+'&name='+e.catename
			});
		},
		//搜索跳转
		toSearch(){
			uni.showToast({title: "建议跳转到新页面做搜索功能"});
		}
	}
}
